.card-col {
  position: relative;
}

.card-col {
  padding-bottom: 25px !important;
}

.btn-card {
  border-radius: 50px !important;
  color: black !important;
}

.btn-card:hover {
  color: white !important;
}

.row-btn {
  justify-content: space-evenly;
  padding-top: 30px;
}

.card-row {
  padding: 45px;
}

.justify-content-md-center {
  padding-top: 40px;
}

/* slider buton  */
.switch {
  position: absolute;
  display: inline-block;
  width: 80px;
  height: 34px;
  top: 0;
  right: 0;
  margin-right: 65px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: dodgerblue;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(45px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.cube {
  border: 1px solid;
  width: 30px;
  position: absolute;
  margin-top: 11px;
  height: 12px;
  margin-left: 5px;
}

@media only screen and (max-width: 426px) {
  .switch {
    top: unset;
    margin-right: 170px;
  }

  .btn-card {
    margin-top: 10px;
  }

  .row-btn {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
}



