.btn-more {
  width: -webkit-fill-available;
  margin-left: 65%;
}

.arrow {
  margin-left: 37%;
}

.card-col {
  position: relative;
}

.card-col {
  padding-bottom: 25px !important;
}

.para-1 {
  position: absolute;
  right: 0;
  margin-right: 20px;
  top: 0;
  margin-top: 12px;
  background-color: orange;
  padding: 6px;
  border-radius: 40px;
  color: black;
}

.link {
  text-decoration: none !important;
  color: black;
}

.link :hover {
  color: gray;
}

@media only screen and (max-width: 426px) {
  .para-1 {
    position: unset;
    width: fit-content;
  }

  .row-details {
    flex-direction: column-reverse !important;
  }

  .btn-more {
    margin-left: 60% !important;
  }
}

@media only screen and (max-width: 768px) {
  .para-1 {
    position: unset;
    width: fit-content;
  }

  .btn-more {
    margin-left: 37%;
  }

  .col-md-5 {
    margin-bottom: 25px;
  }
}
